import React from "react";
import "./style.css";

export default function Monster(props){
    return (
        <div className="monsterWrapper">
        <div className="monster">
            <img src={`img/monsters/${props.name}.png`} alt={props.name} />
            <div className="monsterStats">
                <h2>{props.name}</h2>
                <p>HP: <progress className="nes-progress is-success" value={props.hp} max={props.maxHp} /></p>
            </div>
        </div>
        </div>
    )
}