import React from "react";
import char from "../Data/char";

export default function SelectHero(){
    return (
        <div className="charOverlay">
            <h2>Choose your hero</h2>
            {char.map(o=>{
                return(
                    <div className="selectChar">
                        <img src={o.img} alt={o.name} />
                        <div>
                            <h3>{o.name}</h3>
                            <h4>{o.race}</h4>
                            <h4>{o.class}</h4>
                            <ul className="stats clearfix">
                                <li><span>STR:</span> {o.str}</li>
                                <li><span>DEX:</span> {o.dex}</li>
                                <li><span>CON:</span> {o.con}</li>
                                <li><span>INT:</span> {o.int}</li>
                                <li><span>WIS:</span> {o.wis}</li>
                                <li><span>CHA:</span> {o.cha}</li>
                            </ul>
                            <ul className="clearfix">
                                <li><span>AC:</span> {o.ac}</li>
                                <li><span>HP:</span> {o.hp}</li>
                            </ul>
                        </div>
                    </div>
                )
            })}
        </div>
    )
}