import rollToHit from "./rollToHit";
import rollDice from "./rollDice";

export default function damage(props) {
    console.log('damage ', props)
    let roll = rollToHit(props);
    switch (roll) {
        case 'crit':
            return (rollDice(props.damage[0].damage_dice) + props.damage[0].damage_bonus * 2);
        case 'hit':
            return (rollDice(props.damage[0].damage_dice) + props.damage[0].damage_bonus);
        case 'miss':
            return 0;
        default:
            console.log('default');
    }
}