import React, {useState} from "react";
import "./style.css";

export default function FightControls(props) {
    const [fightState, setFightState] = useState(false);
    const attackType = (name) => {
        let i = props.initiative.hero.actions.findIndex( action => action.name === name );
        let k = Math.floor(Math.random() * (props.initiative.enemy.actions.length));
        console.log('attack type', props)
        return props.fight(
            props.initiative.hero.actions[i], 
            props.initiative.enemy.actions[k],
            props.initiative
        );
    }
    if (fightState) {
        return (
            <React.Fragment>
                {props.initiative.hero.actions.map( o=> 
                    <button onClick={()=> attackType(o.name)}>{o.name}</button>
                )}
                <button onClick={()=> props.endFight()}>Win Fight</button>
            </React.Fragment>
        )
    } else {
        return (
            <React.Fragment>
                <h2>Fight Block</h2>
                <button onClick={()=> setFightState(true)} >Fight</button>
                <button onClick={()=> props.runAway()}>Run Away</button>
            </React.Fragment>
        )
    }
}