// React dependencies
import React, { useState, useEffect } from "react";
import { Redirect } from "react-router-dom";
import "./style.css";

// Data files
import Monsters from "../Data/monsters";
import SelectedHero from "../Data/selectedCharacter";
import Story from "../Data/story";

// Components
import CharInfo from "../Components/CharInfo";
import RoomDisplay from "../Components/RoomDisplay";
import TextBlock from "../Components/TextBlock";

// Game Logic
import rollDice from "../Logic/rollDice";
import rollToHit from "../Logic/rollToHit";
import isAlive from "../Logic/isAlive";
import damage from "../Logic/damage";

// Start Game
export default function Game() {
  const [roomBg, setRoomBg] = useState("room");
  const [hero, setHero] = useState({});
  const [enemy, setEnemy] = useState({});
  const [initiative, setInitiative] = useState({});
  const [story, setStory] = useState({
    ...Story,
    index: 0,
    isFight: false
  });
  useEffect(() => {
    setHero({
      ...SelectedHero,
      isAlive: true
    });
  }, []);
  useEffect(()=> {
    // let monstersArray = function(hero) {
    //   if (hero.lvl > 5) {
    //     return monstersArray = Monsters;
    //   } else if (hero.lvl > 1) {
    //     return monstersArray = Monsters.filter( monsters=> monsters.hp <= 50);
    //   } else if (hero.lvl === 1) {
    //     return monstersArray = Monsters.filter( hp => hp.hp <= 20);
    //   } else {
    //     return console.log('default ', hero.lvl);
    //   }
    // }
    setEnemy({
      ...Monsters[(rollDice(`1d${Monsters.length}`) - 1)]
    });
  },[story.isFight]);
  useEffect(()=> {
    setInitiative( ()=>{
      if (hero.dex >= enemy.dex){
        return {hero, enemy, attacker: hero, defender: enemy}
      }
      else {
        return {hero, enemy, attacker: enemy, defender: hero}
      }
    });
  }, [enemy]);
  const openDoor = () => {
    setRoomBg("room" + Math.floor(Math.random() * 5 + 1));
    setStory({
      ...story,
      index: story.index + 1,
      isFight: true
    });
  };
  const fight = (attName, defName, initiative) => {
    let attacking = {...initiative.attacker}
    let defending = {...initiative.defender}
    let dmg = damage(attName, initiative);
    // console.log(`original hp is ${defending.hp}`);
    // console.log(`${defending.name}'s new HP is ${defending.hp - dmg}`);
    defending.hp = (defending.hp - dmg);

    if (defending.name === initiative.enemy.name) {
      initiative.enemy.hp = defending.hp;
      console.log(initiative);
    } else {
      setHero({
        ...SelectedHero,
        hp: defending.hp
      })
      console.log(hero)
    }
    
    if(isAlive(defending)){
      dmg = damage(defName, attacking);
      attacking.hp = (attacking.hp - dmg);
      if(isAlive(attacking)){
        setInitiative({
          ...initiative,
          defender: attacking,
          attacker: defending
        })
      } else {
        console.log('attacker died')
      }
    } else {
      console.log('defender died')
    }
  };
  const runAway = () => {
      console.log('return');
      return <Redirect to="/credits" />
  };
  function showState() {
    console.log(initiative);
  }
  return (
    <React.Fragment>
      <RoomDisplay roomBg={roomBg} story={story} enemy={initiative.enemy}/>
      <CharInfo {...hero} />
      <TextBlock
        openDoor={openDoor}
        fight={fight}
        runAway={runAway}
        setStory={setStory}
        {...story}
        initiative={initiative}
      />
      <button onClick={() => showState()} className="seeState">see state</button>
    </React.Fragment>
  );
}
