import React, { useState, useEffect } from "react";
import "./style.css";
import FightControls from "../FightControls";

export default function TextBlock(props) {
    const index = props.index;
    const [displayMessage, setDisplayMessage] = useState(props[index].room);
    const lookAround = () => {
        setDisplayMessage(props[index].look);
    };
    useEffect(()=> {
        setDisplayMessage(props[index].room)
    },[props])
    const roomBlock = () => {
        return (
            <div className="textBlock">
                <p>{displayMessage}</p>
                <button onClick={()=> lookAround()}>Look Around</button>
                <button onClick={()=> props.openDoor()}>Open Door</button>
            </div>
        )
    };
    const endFight = () =>{
        props.setStory({
            ...props,
            isFight: false
        })
    };
    const fightBlock = () => {
        return (
            <div className="textBlock">
                <FightControls 
                    endFight={endFight} 
                    runAway={props.runAway}
                    fight={props.fight}
                    initiative={props.initiative}
                />
            </div>
        )
    };
    return (props.isFight) ? fightBlock() : roomBlock()
}