export default function rollDice(props) {
  const regexp = /(?<num>[0-9])(?:d)(?<sided>[0-9]*)/;
  let roll = 0;
  let {
    groups: { num, sided }
  } = regexp.exec(props);
  num = parseInt(num);
  sided = parseInt(sided);
  for (let i = 0; i < num; i++) {
    roll += Math.floor(Math.random() * sided + 1);
  }
  return roll;
}
