import rollDice from "./rollDice";
//props is initiative

// need to calculate attack bonus - which means i need to determine which action is used. For monsters should this be random? sequential? for heroes this is the selected action
export default function rollToHit(props) {
    console.log("rth ", props)
    let opponent = 15;
    let roll = rollDice('1d20');
    if (roll === 20) {
        return 'crit';
    } else if ( (roll + props.attack_bonus) >= opponent) {
        return 'hit';
    } else {
        console.log(roll);
        return 'miss'
    }
}