import React from "react";
import "./style.css";

export default function CharInfo(props) {
    return (
        <div className="charInfo">
            <img src={props.img} alt={props.name} />
            <div>
                <h3>{props.name}</h3>
                <p>
                    HP:
                    <progress id="heroHp" value={props.hp} max={props.maxHp} class="nes-progress is-success"></progress>
                </p>
                <p>
                    XP:
                    <progress id="heroXp" value={props.xp} max={props.nextLvl} class="nes-progress is-primary"></progress>
                </p>
            </div>
        </div>
    )
}